import { useCallback, useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
// @mui
import { FormControl, InputLabel } from '@mui/material';
import Alert from '@mui/material/Alert';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import Container from '@mui/material/Container';
import MenuItem from '@mui/material/MenuItem';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableContainer from '@mui/material/TableContainer';
// utils
import { fTimestamp } from 'src/utils/format-time';
// hooks
import { useBoolean } from 'src/hooks/use-boolean';
// components
import { ConfirmDialog } from 'src/components/custom-dialog';
import Iconify from 'src/components/iconify';
import Scrollbar from 'src/components/scrollbar';
import { useSnackbar } from 'src/components/snackbar';
import {
  emptyRows,
  getComparator,
  TableEmptyRows,
  TableHeadCustom,
  TableNoData,
  TablePaginationCustom,
  TableSelectedAction,
  useTable,
} from 'src/components/table';
// type
import { FiltersTableProps } from 'src/types/table';
// utils
import { updateClass } from 'src/api/controllers/class';
// child
import TableFiltersResult from './table-filters-result';
import TableRowData from './table-row-data';
import TableToolbar from './table-toolbar';
// context
import { useTableContext } from './context';

// ----------------------------------------------------------------------

const defaultFilters: FiltersTableProps = {
  name: '',
  type: [],
  startDate: null,
  endDate: null,
};

type TableProps = {
  grades?: any;
};

// ----------------------------------------------------------------------

export default function TableData({ grades }: TableProps) {
  const table = useTable({ defaultOrderBy: 'orderNumber' });

  const { _table_data, _table_header, onSetTableData, onUpdateRow, _table_config, onSetOpenForm } =
    useTableContext();

  const confirmDelete = useBoolean();

  const { enqueueSnackbar } = useSnackbar();

  // ----------------------------------------------------------------------

  const [tableData, setTableData] = useState(_table_data);

  useEffect(() => {
    setTableData(_table_data);
  }, [_table_data]);

  const [filters, setFilters] = useState(defaultFilters);

  const dateError =
    filters?.startDate && filters?.endDate
      ? filters?.startDate?.getTime() > filters?.endDate?.getTime()
      : false;

  const dataFiltered = applyFilter({
    inputData: tableData,
    comparator: getComparator(table.order, table.orderBy),
    filters,
    dateError,
  });

  const dataInPage = dataFiltered?.slice(
    table.page * table.rowsPerPage,
    table.page * table.rowsPerPage + table.rowsPerPage
  );

  const denseHeight = table.dense ? 52 : 72;

  const canReset =
    !!filters.name || filters.type.length !== 0 || (!!filters.startDate && !!filters.endDate);

  const notFound = (!dataFiltered?.length && canReset) || !dataFiltered?.length;

  // ----------------------------------------------------------------------

  // handle filter d liu
  const handleFilters = useCallback(
    (name: string, value: any) => {
      table.onResetPage();
      setFilters((prevState: any) => ({
        ...prevState,
        [name]: value,
      }));
    },
    [table]
  );

  // Xo 1 hng trong bng
  const handleDeleteRow = useCallback(
    async (id: number) => {
      try {
        // @ts-ignore
        await sdk.course.offline[_table_config.table_name].deleteOne(id);

        const deleteRow = tableData.filter((row: any) => row.id !== id);

        setTableData(deleteRow);

        onSetTableData(deleteRow);

        table.onUpdatePageDeleteRow(dataInPage?.length);

        enqueueSnackbar(' xo d liu thnh cng !');
      } catch (error) {
        enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
      }
    },

    [dataInPage?.length, table, tableData, _table_config]
  );

  // Xo nhiu hng ca bng
  const handleDeleteRows = useCallback(async () => {
    try {
      // @ts-ignore
      await sdk.course.offline[_table_config.table_name].deleteMany(table.selected);

      const deleteRows = tableData.filter((row: any) => !table.selected.includes(row.id));

      setTableData(deleteRows);

      onSetTableData(deleteRows);

      table.onUpdatePageDeleteRows({
        totalRows: tableData?.length,
        totalRowsInPage: dataInPage?.length,
        totalRowsFiltered: dataFiltered?.length,
      });

      enqueueSnackbar(` xo ${table.selected.length} hng d liu thnh cng !`);
    } catch (error) {
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  }, [dataFiltered?.length, dataInPage?.length, table, tableData]);

  // ----------------------------------------------------------------------

  const [gradeValue, setGradeValue] = useState<any>('');
  const [selectedValue, setSelectedValue] = useState<any>('');

  const confirmStudentGrant = useBoolean();

  // Chia lp cho hc sinh

  const handleGrantStudent = useCallback(async () => {
    try {
      const formData = {
        students: {
          set: table.selected.map((student) => ({ id: student })),
        },
      };

      if (!selectedValue) {
        enqueueSnackbar('Bn cha chn lp !', { variant: 'error' });
      } else {
        const updated = await updateClass(formData, selectedValue);

        updated?.students?.forEach((student: any) => {
          onUpdateRow(student);
        });
        confirmStudentGrant.onFalse();
        enqueueSnackbar(' phn b thnh cng !');
      }
    } catch (error) {
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  }, [table, selectedValue]);

  const handleResetFilters = useCallback(() => {
    setFilters(defaultFilters);
  }, []);

  const handleSelectedAllRow = (checked: boolean) => {
    table.onSelectAllRows(
      checked,
      tableData.map((row: any) => row.id)
    );
  };

  return (
    <>
      <Container maxWidth={false}>
        {_table_config.add_new && (
          <Button
            variant="contained"
            color="primary"
            sx={{ marginBottom: 3 }}
            endIcon={<Iconify icon="material-symbols:add-notes" />}
            onClick={() => onSetOpenForm(true)}
          >
            Thm d liu mi
          </Button>
        )}

        {_table_config.allocation_student && (
          <Alert variant="outlined" severity="info" sx={{ mb: 1 }}>
            Ghi ch :  phn b hc sinh, hy <strong>tick chn hc sinh</strong> cn phn b sau
             bm chuyn lp, chn trng lp cn chuyn.
          </Alert>
        )}
        <Card>
          <TableToolbar
            // @ts-ignore
            options={[...new Set(_table_data?.map((data: any) => data.type))]}
            filters={filters}
            onFilters={handleFilters}
            //
            canReset={canReset}
            onResetFilters={handleResetFilters}
          />

          {canReset && (
            <TableFiltersResult
              filters={filters}
              onFilters={handleFilters}
              //
              onResetFilters={handleResetFilters}
              //
              results={dataFiltered?.length}
              sx={{ p: 2.5, pt: 0 }}
            />
          )}

          <TableContainer sx={{ position: 'relative', overflow: 'unset' }}>
            <TableSelectedAction
              dense={table.dense}
              numSelected={table.selected.length}
              rowCount={tableData?.length}
              onSelectAllRows={_table_config.table_selected && handleSelectedAllRow}
              action={
                <Stack direction="row" spacing={1}>
                  {_table_config.allocation_student && (
                    <Button
                      size="small"
                      variant="contained"
                      color="primary"
                      onClick={confirmStudentGrant.onTrue}
                      endIcon={<Iconify width={15} icon="eos-icons:storage-class" />}
                    >
                      Chuyn lp
                    </Button>
                  )}

                  {_table_config.delete_all && (
                    <Button
                      size="small"
                      variant="contained"
                      color="error"
                      onClick={confirmDelete.onTrue}
                      endIcon={<Iconify width={15} icon="solar:trash-bin-trash-bold" />}
                    >
                      Xo la chn
                    </Button>
                  )}
                </Stack>
              }
            />

            <Scrollbar>
              <Table size={table.dense ? 'small' : 'medium'} sx={{ minWidth: 960 }}>
                <TableHeadCustom
                  order={table.order}
                  orderBy={table.orderBy}
                  headLabel={_table_header}
                  rowCount={tableData?.length}
                  numSelected={table.selected.length}
                  onSort={table.onSort}
                  onSelectAllRows={_table_config.table_selected && handleSelectedAllRow}
                  sx={{ whiteSpace: 'nowrap' }}
                />

                <TableBody>
                  {dataFiltered
                    ?.slice(
                      table.page * table.rowsPerPage,
                      table.page * table.rowsPerPage + table.rowsPerPage
                    )
                    .map((row: any) => (
                      <TableRowData
                        key={row.id}
                        row={row}
                        selected={table.selected.includes(row.id)}
                        onSelectRow={() => table.onSelectRow(row.id)}
                        onDeleteRow={() => handleDeleteRow(row.id)}
                      />
                    ))}

                  <TableEmptyRows
                    height={denseHeight}
                    emptyRows={emptyRows(table.page, table.rowsPerPage, tableData?.length)}
                  />

                  <TableNoData notFound={notFound} />
                </TableBody>
              </Table>
            </Scrollbar>
          </TableContainer>

          <TablePaginationCustom
            count={dataFiltered?.length}
            page={table.page}
            rowsPerPage={table.rowsPerPage}
            onPageChange={table.onChangePage}
            onRowsPerPageChange={table.onChangeRowsPerPage}
            //
            dense={table.dense}
            onChangeDense={table.onChangeDense}
          />
        </Card>
      </Container>

      <ConfirmDialog
        open={confirmDelete.value}
        onClose={confirmDelete.onFalse}
        title="Xc nhn xo d liu"
        content={
          <>
            Bn c chc mun xo <strong> {table.selected.length} </strong> dng d liu ny ? ( d
            liu  xo s <strong>khng th khi phc</strong> li c na !! )
          </>
        }
        action={
          <Button
            variant="contained"
            color="error"
            onClick={() => {
              handleDeleteRows();
              confirmDelete.onFalse();
            }}
          >
            Xo d liu
          </Button>
        }
      />

      <ConfirmDialog
        open={confirmStudentGrant.value}
        onClose={confirmStudentGrant.onFalse}
        title="Chuyn hc sinh sang lp khc"
        content={
          <Stack spacing={3} mt={3}>
            <FormControl>
              <InputLabel>La chn khi lp...</InputLabel>
              <Select
                label="La chn khi lp..."
                value={gradeValue}
                onChange={(event: any) => setGradeValue(event.target.value)}
                sx={{ width: '100%' }}
              >
                {grades?.map((option: any) => (
                  <MenuItem key={option?.id} value={option}>
                    {`Khi lp ${option?.label}`}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>

            <FormControl>
              <InputLabel>La chn lp hc...</InputLabel>
              <Select
                disabled={!gradeValue}
                label="La chn lp hc..."
                value={selectedValue}
                onChange={(event: any) => setSelectedValue(event.target.value)}
                sx={{ width: '100%' }}
              >
                {gradeValue?.classes?.map((option: any) => (
                  <MenuItem key={option?.value} value={option?.id}>
                    {`Lp ${gradeValue?.label}${option?.name}`}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          </Stack>
        }
        action={
          <Button variant="contained" color="primary" onClick={handleGrantStudent}>
            Tin hnh chuyn lp
          </Button>
        }
      />
    </>
  );
}

// ----------------------------------------------------------------------

function applyFilter({
  inputData,
  comparator,
  filters,
  dateError,
}: {
  inputData: any;
  comparator: (a: any, b: any) => number;
  filters: any;
  dateError: boolean;
}) {
  const { name, type, startDate, endDate } = filters;

  const stabilizedThis = inputData?.map((el: any, index: number) => [el, index] as const);

  stabilizedThis?.sort((a: any, b: any) => {
    const data = comparator(a[0], b[0]);
    if (data !== 0) return data;
    return a[1] - b[1];
  });

  inputData = stabilizedThis?.map((el: any) => el[0]);

  if (name) {
    inputData = inputData?.filter(
      (data: any) => data?.name?.toLowerCase().indexOf(name.toLowerCase()) !== -1
    );
  }

  if (type.length) {
    inputData = inputData.filter((data: any) => type.includes(data.type));
  }

  if (!dateError) {
    if (startDate && endDate) {
      inputData = inputData?.filter(
        (data: any) =>
          fTimestamp(data.createdAt) >= fTimestamp(startDate) &&
          fTimestamp(data.createdAt) <= fTimestamp(endDate)
      );
    }
  }

  return inputData;
}
