import * as XLSX from 'xlsx';

// ----------------------------------------------------------------------

type ExportExcelProps = {
  data: any;
  name: string;
  sheet: string;
  type: string;
};

export const exportToExcel = ({ data, name, sheet, type }: ExportExcelProps) => {
  const workbook = XLSX.utils.book_new();
  const worksheet = XLSX.utils.json_to_sheet(data);
  XLSX.utils.book_append_sheet(workbook, worksheet, sheet);
  XLSX.writeFile(workbook, `${name}${type}`);
};

export const readExcelFile = async (file: any) => {
  const data = await file?.arrayBuffer();
  const workbook = XLSX.read(data);

  const worksheet = workbook.Sheets[workbook.SheetNames[0]];
  const jsonData = XLSX.utils.sheet_to_json(worksheet);

  return jsonData;
};

export const readExcel = async () => {
  const path = '/assets/import/import.xlsx';

  const xhr = new XMLHttpRequest();
  xhr.open('GET', path, true);
  xhr.responseType = 'arraybuffer';

  xhr.onload = (e) => {
    const arraybuffer = xhr.response;

    // Chuyn i arraybuffer thnh mt mng d liu
    const data1 = new Uint8Array(arraybuffer);

    // S dng th vin xlsx  c d liu t tp Excel
    const workbook = XLSX.read(data1, { type: 'array' });

    // Ly d liu t tp Excel (v d: ly d liu t sheet u tin)
    const sheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[sheetName];

    // Chuyn i d liu t nh dng xlsx sang nh dng JSON
    const data = XLSX.utils.sheet_to_json(worksheet);

    console.log(data);
  };

  xhr.send();
};
