// react
import { useEffect, useState } from 'react';
// sdk
import sdk from '@iit/sdk';
import { Response, Question } from '@iit/sdk/dist/types';

// ----------------------------------------------------------------------

export default function useGetQuestion() {
  const [data, setData] = useState<Response<Question[]>>({});

  useEffect(() => {
    const handleFetch = async () => {
      const response = await sdk.course.offline.question.findMany({});
      if (response) setData(response);
    };

    handleFetch();
  }, []);

  return data;
}

// ----------------------------------------------------------------------

export async function createQuestion(question: any) {
  const { data } = await sdk.course.offline.question.create(question);

  return data;
}

// ----------------------------------------------------------------------

export async function updateQuestion(question: any, id: string) {
  const { data } = await sdk.course.offline.question.update(id, question);

  return data;
}

// ----------------------------------------------------------------------

export async function createManyQuestion(questions: any) {
  const { data } = await sdk.course.offline.question.createMany(questions);

  return data;
}
