import { useCallback } from 'react';
// @mui
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import FormControl from '@mui/material/FormControl';
import InputAdornment from '@mui/material/InputAdornment';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import OutlinedInput from '@mui/material/OutlinedInput';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
// components
import { usePopover } from 'src/components/custom-popover';
import Iconify from 'src/components/iconify';

import { FiltersTableProps } from 'src/types/table';
// hook
import { useBoolean } from 'src/hooks/use-boolean';
// locales
import { useLocales } from 'src/locales';
// context
import { useTableContext } from './context';
// child
import ExportTableData from './action/export-table-data';
import ImportTableData from './action/import-table-data';

// ----------------------------------------------------------------------

type Props = {
  options: any;
  //
  filters: FiltersTableProps;
  onFilters: (name: string, value: string | string[] | Date | null) => void;
  //
  canReset: boolean;
  onResetFilters: VoidFunction;
};

export default function TableToolbar({
  options,
  //
  filters,
  onFilters,
  //
  canReset,
  onResetFilters,
}: Props) {
  const { _table_config } = useTableContext();

  const { t } = useLocales();

  const popover = usePopover();

  const confirmImport = useBoolean();

  const confirmExport = useBoolean();

  const handleFilterName = useCallback(
    (event: React.ChangeEvent<HTMLInputElement>) => {
      onFilters('name', event.target.value);
    },
    [onFilters]
  );

  const handleFilterType = useCallback(
    (event: SelectChangeEvent<string[]>) => {
      onFilters(
        'type',
        typeof event.target.value === 'string' ? event.target.value.split(',') : event.target.value
      );
    },
    [onFilters]
  );

  const handleFilterStartDate = useCallback(
    (newValue: Date | null) => {
      onFilters('startDate', newValue);
    },
    [onFilters]
  );

  const handleFilterEndDate = useCallback(
    (newValue: Date | null) => {
      onFilters('endDate', newValue);
    },
    [onFilters]
  );

  return (
    <>
      <Stack
        spacing={2}
        alignItems={{ xs: 'flex-end', md: 'center' }}
        direction={{
          xs: 'column',
          md: 'row',
        }}
        sx={{
          p: 2.5,
          pr: { xs: 2.5, md: 1 },
        }}
      >
        {_table_config.filter_by_date && (
          <>
            <DatePicker
              label={t('T ngy...')}
              value={filters.startDate}
              onChange={handleFilterStartDate}
              slotProps={{
                textField: {
                  fullWidth: true,
                },
              }}
              sx={{
                maxWidth: { md: 200 },
              }}
            />

            <DatePicker
              label={t('n ngy...')}
              value={filters.endDate}
              onChange={handleFilterEndDate}
              slotProps={{ textField: { fullWidth: true } }}
              sx={{
                maxWidth: { md: 200 },
              }}
            />
          </>
        )}

        <Stack direction="row" alignItems="center" spacing={2} flexGrow={1} sx={{ width: 1 }}>
          {_table_config.filter_by_type && (
            <FormControl
              sx={{
                flexShrink: 0,
                width: { xs: 1, md: 200 },
              }}
            >
              <InputLabel>{t('Phn loi...')}</InputLabel>

              <Select
                multiple
                value={filters.type}
                onChange={handleFilterType}
                input={<OutlinedInput label={t('Phn loi...')} />}
                renderValue={(selected) => selected.map((value: any) => value).join(', ')}
                MenuProps={{
                  PaperProps: {
                    sx: { maxHeight: 240 },
                  },
                }}
              >
                {options?.map((option: any) => (
                  <MenuItem key={option} value={option}>
                    <Checkbox disableRipple size="small" checked={filters.type.includes(option)} />
                    {option}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          )}

          {_table_config.filter_by_search && (
            <TextField
              fullWidth
              value={filters.name}
              onChange={handleFilterName}
              placeholder={t('Nhp t kho d liu tm kim...')}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <Iconify icon="eva:search-fill" sx={{ color: 'text.disabled' }} />
                  </InputAdornment>
                ),
              }}
            />
          )}

          <Stack spacing={1} direction="row" alignItems="center">
            {_table_config.import_data && (
              <Button
                variant="contained"
                size="small"
                color="primary"
                onClick={() => {
                  confirmImport.onTrue();
                  popover.onClose();
                }}
                startIcon={<Iconify icon="file-icons:microsoft-excel" />}
                sx={{ whiteSpace: 'nowrap' }}
              >
                {t('Import d liu')}
              </Button>
            )}

            {_table_config.export_data && (
              <Button
                variant="contained"
                size="small"
                color="success"
                onClick={() => {
                  confirmExport.onTrue();
                  popover.onClose();
                }}
                startIcon={<Iconify icon="file-icons:microsoft-excel" />}
                sx={{ whiteSpace: 'nowrap' }}
              >
                {t('Xut d liu')}
              </Button>
            )}
          </Stack>
        </Stack>

        {canReset && (
          <Button
            color="error"
            sx={{ flexShrink: 0 }}
            onClick={onResetFilters}
            startIcon={<Iconify icon="fa:refresh" />}
          >
            Lm mi
          </Button>
        )}
      </Stack>

      <ImportTableData open={confirmImport.value} onClose={() => confirmImport.onFalse()} />

      <ExportTableData open={confirmExport.value} onClose={() => confirmExport.onFalse()} />
    </>
  );
}
