// react
import { useState } from 'react';
// MUI
import Button from '@mui/material/Button';
import FormControlLabel from '@mui/material/FormControlLabel';
import InputAdornment from '@mui/material/InputAdornment';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
// components
import { ConfirmDialog } from 'src/components/custom-dialog';
import Iconify from 'src/components/iconify';
// utils
import { exportToExcel } from 'src/utils/xlsx';
// context
import { useTableContext } from '../context';

type Props = {
  open: boolean;
  onClose: VoidFunction;
};

// ----------------------------------------------------------------------

export default function ExportTableData({ open, onClose }: Props) {
  const { _table_export_data } = useTableContext();

  const [fileName, setFileName] = useState('data_export');

  const [sheetName, setSheetName] = useState('Sheet1');

  const [fileType, setFileType] = useState('.xlsx');

  const handleExportData = () => {
    exportToExcel({
      data: _table_export_data,
      name: fileName,
      sheet: sheetName,
      type: fileType,
    });

    onClose();
  };
  return (
    <ConfirmDialog
      open={open}
      onClose={onClose}
      title="Xut d liu ra file excel"
      content={
        <Stack spacing={2}>
          <Typography variant="subtitle2">t tn cho file :</Typography>

          <TextField
            fullWidth
            value={fileName}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              setFileName(event.target.value)
            }
            placeholder="Nhp tn file..."
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify icon="file-icons:microsoft-excel" sx={{ color: 'success.main' }} />
                </InputAdornment>
              ),
            }}
          />

          <Typography variant="subtitle2">Sheet lu mc nh :</Typography>

          <TextField
            fullWidth
            value={sheetName}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              setSheetName(event.target.value)
            }
            placeholder="Nhp sheet mc nh..."
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify icon="dashicons:media-spreadsheet" sx={{ color: 'success.main' }} />
                </InputAdornment>
              ),
            }}
          />

          <Typography variant="subtitle2">nh dng cho file :</Typography>

          <RadioGroup
            row
            defaultValue=".xlsx"
            value={fileType}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              setFileType(event.target.value)
            }
          >
            <FormControlLabel
              value=".xlsx"
              control={<Radio color="success" />}
              label="nh dng .xlsx"
            />
            <FormControlLabel
              value=".csv"
              control={<Radio color="success" />}
              label="nh dng .csv"
            />
          </RadioGroup>
        </Stack>
      }
      action={
        <Button variant="contained" color="success" onClick={handleExportData}>
          Xut d liu
        </Button>
      }
    />
  );
}
