// react
import React, { useRef, useState } from 'react';
// MUI
import Button from '@mui/material/Button';
import FormControlLabel from '@mui/material/FormControlLabel';
import InputAdornment from '@mui/material/InputAdornment';
import MenuItem from '@mui/material/MenuItem';
import OutlinedInput from '@mui/material/OutlinedInput';
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import Select from '@mui/material/Select';
import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Typography from '@mui/material/Typography';
// components
import { ConfirmDialog } from 'src/components/custom-dialog';
import Iconify from 'src/components/iconify';
import { useSnackbar } from 'src/components/snackbar';
//
import useGetSubject from 'src/api/controllers/subject';
// utils
import { useBoolean } from 'src/hooks/use-boolean';
import { exportToExcel, readExcelFile } from 'src/utils/xlsx';
// api
import { createManyQuestion } from 'src/api/controllers/question';
// context
import { useTableContext } from '../context';

type Props = {
  open: boolean;
  onClose: VoidFunction;
};

// ----------------------------------------------------------------------

export default function ImportTableData({ open, onClose }: Props) {
  const { _table_import_data, onCreateNewRow, onCreateNewManyRow } = useTableContext();

  const { enqueueSnackbar } = useSnackbar();

  const { data: subjects } = useGetSubject();

  const isLoading = useBoolean();

  const [fileName, setFileName] = useState('import_file');

  const [fileType, setFileType] = useState('.xlsx');

  const [subjectId, setSubjectId] = useState('');

  const [topicId, setTopicId] = useState('');

  const filterSubject = subjects?.filter((subject: any) => subject?.id === subjectId)[0];

  const [importData, setImportData] = useState([]);

  const isImport = importData.length > 0;

  const inputRef = useRef<any>(null);

  const handleClick = () => {
    inputRef.current.click();
  };

  const handleExportData = () => {
    exportToExcel({
      data: _table_import_data.sample_data,
      name: fileName,
      sheet: 'Sheet1',
      type: fileType,
    });
  };

  const handleImportValue = async (event: any) => {
    const data = (await readExcelFile(event.target.files[0])) as any;
    setImportData(data);
  };

  const onImportData = async () => {
    isLoading.onTrue();

    try {
      const created = await createManyQuestion(
        importData?.map((importValue: any) => ({
          content: importValue?.['Tn cu hi'],
          type: importValue?.['Loi cu hi'] === 'Trc nghim' ? 'choice' : 'essay',
          point: importValue?.['im cu hi'],
          level: importValue?.[' kh'],
          line: importValue?.['S hng ( cu t lun )'],
          answer01: importValue?.['p n s 1'],
          answer02: importValue?.['p n s 2'],
          answer03: importValue?.['p n s 3'],
          answer04: importValue?.['p n s 4'],
          topic: {
            connect: { id: +topicId },
          },
        }))
      );

      // @ts-ignore
      if (created?.length > 0) {
        onCreateNewManyRow(created);
      } else {
        onCreateNewRow(created);
      }

      isLoading.onFalse();

      enqueueSnackbar(' thm cu hi thnh cng !');

      setImportData([]);

      onClose();
    } catch (error) {
      isLoading.onFalse();
      enqueueSnackbar(' c li xy ra ! Vui lng th li !', { variant: 'error' });
    }
  };
  return (
    <ConfirmDialog
      open={open}
      onClose={onClose}
      title="Nhp d liu t file excel"
      content={
        <Stack spacing={2}>
          <Typography variant="subtitle2">t tn cho file :</Typography>

          <TextField
            fullWidth
            value={fileName}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              setFileName(event.target.value)
            }
            placeholder="Nhp tn file..."
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Iconify icon="file-icons:microsoft-excel" sx={{ color: 'primary.main' }} />
                </InputAdornment>
              ),
            }}
          />

          <Typography variant="subtitle2">nh dng cho file :</Typography>

          <RadioGroup
            row
            defaultValue=".xlsx"
            value={fileType}
            onChange={(event: React.ChangeEvent<HTMLInputElement>) =>
              setFileType(event.target.value)
            }
          >
            <FormControlLabel value=".xlsx" control={<Radio />} label="nh dng .xlsx" />

            <FormControlLabel value=".csv" control={<Radio />} label="nh dng .csv" />
          </RadioGroup>

          <>
            <Typography variant="subtitle2">Chn mn hc :</Typography>

            <Select
              value={subjectId}
              onChange={(event: any) => setSubjectId(event.target.value)}
              input={<OutlinedInput />}
              MenuProps={{
                PaperProps: {
                  sx: { maxHeight: 240 },
                },
              }}
            >
              {subjects?.map((option: any) => (
                <MenuItem key={option.value} value={option.id}>
                  {`${option?.name} ${option?.grades
                    ?.map((grade: any) => `${grade?.label}`)
                    ?.join('')}`}
                </MenuItem>
              ))}
            </Select>

            <Typography variant="subtitle2">Chn ch  :</Typography>

            <Select
              disabled={!subjectId}
              value={topicId}
              onChange={(event: any) => setTopicId(event.target.value)}
              input={<OutlinedInput />}
              MenuProps={{
                PaperProps: {
                  sx: { maxHeight: 240 },
                },
              }}
            >
              {filterSubject?.topics?.map((option: any) => (
                <MenuItem key={option.value} value={option.id}>
                  {option.name}
                </MenuItem>
              ))}
            </Select>
          </>

          <Button variant="contained" onClick={handleExportData}>
            To file d liu mu
          </Button>

          <Button variant="contained" color="primary" onClick={handleClick}>
            Import d liu t file Excel
            <input
              type="file"
              style={{ display: 'none' }}
              accept=".xlsx,.xls"
              ref={inputRef}
              onChange={handleImportValue}
            />
          </Button>
        </Stack>
      }
      action={
        <Button variant="contained" color="primary" onClick={onImportData} disabled={!isImport}>
          Bt u import
        </Button>
      }
    />
  );
}
