// date
import { format } from 'date-fns';
// mui
import ListItemText from '@mui/material/ListItemText';
import TableCell from '@mui/material/TableCell';

// ----------------------------------------------------------------------

type TableTimeLogProps = {
  createdAt: Date;
  updatedAt: Date;
};

export default function TableTimeLog({ createdAt, updatedAt }: TableTimeLogProps) {
  return (
    <>
      <TableCell align="center">
        <ListItemText
          primary={createdAt ? format(new Date(createdAt), 'dd/MM/yyyy') : 'Khng xc nh'}
          secondary={createdAt ? format(new Date(createdAt), 'p') : 'Khng xc nh'}
          primaryTypographyProps={{ typography: 'body2', noWrap: true }}
          secondaryTypographyProps={{
            mt: 0.5,
            component: 'span',
            typography: 'caption',
          }}
        />
      </TableCell>

      <TableCell align="center">
        <ListItemText
          primary={updatedAt ? format(new Date(updatedAt), 'dd/MM/yyyy') : 'Khng xc nh'}
          secondary={updatedAt ? format(new Date(updatedAt), 'p') : 'Khng xc nh'}
          primaryTypographyProps={{ typography: 'body2', noWrap: true }}
          secondaryTypographyProps={{
            mt: 0.5,
            component: 'span',
            typography: 'caption',
          }}
        />
      </TableCell>
    </>
  );
}
