// @mui
import Button from '@mui/material/Button';
import Checkbox from '@mui/material/Checkbox';
import Collapse from '@mui/material/Collapse';
import IconButton from '@mui/material/IconButton';
import MenuItem from '@mui/material/MenuItem';
import Stack from '@mui/material/Stack';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
// hooks
import { useBoolean } from 'src/hooks/use-boolean';
// components
import { ConfirmDialog } from 'src/components/custom-dialog';
import CustomPopover, { usePopover } from 'src/components/custom-popover';
import Iconify from 'src/components/iconify';
import Label from 'src/components/label';
// context
import { useTableContext } from './context';
// children
import TableTimeLog from './table-timelog';

// ----------------------------------------------------------------------

type Props = {
  row: any;
  selected: boolean;
  onSelectRow: VoidFunction;
  onDeleteRow: VoidFunction;
};

export default function TableRowData({ row, selected, onSelectRow, onDeleteRow }: Props) {
  const { _table_rows, _table_row_collapse, _table_config, onSetOpenForm, onSetTableRowSelected } =
    useTableContext();

  const hasAction = _table_config.edit_row || _table_config.delete_row;

  const confirm = useBoolean();

  const collapse = useBoolean();

  const popover = usePopover();

  const renderPrimary = (
    <TableRow hover selected={selected}>
      {_table_config.table_selected && (
        <TableCell padding="checkbox">
          <Checkbox checked={selected} onClick={onSelectRow} />
        </TableCell>
      )}

      {_table_rows && _table_rows(row)}

      <TableTimeLog createdAt={row?.createdAt} updatedAt={row?.updatedAt} />

      {_table_config.collapse_item && (
        <TableCell sx={{ px: 1, whiteSpace: 'nowrap' }}>
          <Stack
            direction="row"
            spacing={1}
            alignItems="center"
            justifyContent="center"
            onClick={collapse.onToggle}
          >
            <Label sx={{ cursor: 'pointer' }} variant="filled">
              Xem danh sch
            </Label>

            <IconButton
              color={collapse.value ? 'inherit' : 'default'}
              sx={{
                ...(collapse.value && {
                  bgcolor: 'action.hover',
                }),
              }}
            >
              <Iconify
                icon={collapse.value ? 'eva:arrow-ios-upward-fill' : 'eva:arrow-ios-downward-fill'}
              />
            </IconButton>
          </Stack>
        </TableCell>
      )}

      <TableCell align="right">
        <IconButton
          disabled={!hasAction}
          color={popover.open ? 'inherit' : 'default'}
          onClick={popover.onOpen}
        >
          <Iconify icon="eva:more-vertical-fill" />
        </IconButton>
      </TableCell>
    </TableRow>
  );

  const renderSecondary = (
    <TableRow>
      <TableCell sx={{ p: 0, border: 'none' }} colSpan={8}>
        <Collapse
          in={collapse.value}
          timeout="auto"
          unmountOnExit
          sx={{ bgcolor: 'background.neutral' }}
        >
          {_table_row_collapse && _table_row_collapse(row)}
        </Collapse>
      </TableCell>
    </TableRow>
  );

  const handleEditRow = () => {
    onSetTableRowSelected(row);
    popover.onClose();
    onSetOpenForm(true);
  };

  return (
    <>
      {renderPrimary}

      {renderSecondary}

      <CustomPopover
        open={popover.open}
        onClose={popover.onClose}
        arrow="right-top"
        sx={{ width: 180 }}
      >
        {_table_config.edit_row && (
          <MenuItem onClick={handleEditRow} sx={{ color: 'warning.main' }}>
            <Iconify icon="material-symbols:box-edit" />
            Chnh sa d liu
          </MenuItem>
        )}

        {_table_config.delete_row && (
          <MenuItem
            onClick={() => {
              confirm.onTrue();
              popover.onClose();
            }}
            sx={{ color: 'error.main' }}
          >
            <Iconify icon="solar:trash-bin-trash-bold" />
            Xo b d liu
          </MenuItem>
        )}
      </CustomPopover>

      <ConfirmDialog
        open={confirm.value}
        onClose={confirm.onFalse}
        title="Xc nhn xo d liu"
        content={
          <>
            Bn c chc mun xo d liu ny ? ( D liu  xo{' '}
            <strong>khng th khi phc li</strong> c na !! )
          </>
        }
        action={
          <Button variant="contained" color="error" onClick={onDeleteRow}>
            Xo b
          </Button>
        }
      />
    </>
  );
}
